import { useEffect } from 'react';
import { platformCollection } from '@src/configs/platform';

const { entities: platformEntities } = platformCollection;

/**
 * @param {PlatformEntity | CourseEntity} platform
 */
export const useDescription = platform => {
  useEffect(() => {
    if (!platform) {
      return;
    }

    const description = document.querySelector('[name="description"]');

    if ('code' in platform) {
      description.setAttribute('content', platformEntities[platform.code]?.slogan);
    } else {
      description.setAttribute('content', platform.brief || '');
    }
  }, [platform]);
};
