import { useMemo } from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { platformEntityList } from '@src/configs/platform';
import { useTheme } from '@src/hooks';
import { GlobalStateProvider } from '@src/components';
import { HomePage } from './routes/HomePage';
import { IndexPage } from './routes/index';
import { DetailPage } from './routes/detail';

function App() {
  const { theme: themeConfig } = useTheme();
  const theme = useMemo(() => createMuiTheme(themeConfig), [themeConfig]);

  const indexPath = useMemo(() => {
    return platformEntityList.map(p => [`/${p.code}/index`, `/${p.abbr}/index`]).flat();
  }, []);

  const detailPath = useMemo(() => {
    return platformEntityList
      .map(p => [`/${p.code}/courses/:courseId`, `/${p.abbr}/c/:courseId`])
      .flat();
  }, []);

  return (
    <GlobalStateProvider>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Router>
          <Switch>
            <Route strict exact path="/">
              <HomePage />
            </Route>
            <Route strict exact path={indexPath}>
              <IndexPage />
            </Route>
            <Route strict exact path={detailPath}>
              <DetailPage />
            </Route>
            <Redirect to="/" />
          </Switch>
        </Router>
      </ThemeProvider>
    </GlobalStateProvider>
  );
}

export { App };
