import { useEffect } from 'react';

/**
 * @param {PlatformEntity} platform
 */
export const usePageFavicon = platform => {
  useEffect(() => {
    const favicons = Array.from(document.querySelectorAll('[rel="icon"],[rel="apple-touch-icon"]'));

    const suffix = platform.code === 'dedao' ? 'png' : 'jpg';
    favicons.forEach(favicon => {
      favicon.setAttribute('href', `/yuer/${platform.code}.${suffix}`);
    });
  }, [platform]);
};
