import { useMount, useUpdateEffect, useWindowScroll } from 'react-use';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import {
  usePlatform,
  useCourseCollection,
  useDocumentTitle,
  usePageFavicon,
  useDescription,
} from '@src/hooks';
import { TitleBar, CourseSearch } from '@src/components';
import { CourseList } from './CourseList';

const useStyles = makeStyles(theme => ({
  container: {
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
  },
  actionIcon: {
    marginRight: theme.spacing(2),
  },
}));

let pageScrollTop = 0;

export function IndexPage() {
  const classes = useStyles();
  const { y } = useWindowScroll();

  const platform = usePlatform();
  const { courseCollection } = useCourseCollection({ platform });

  useDocumentTitle(platform.name);
  usePageFavicon(platform);
  useDescription(platform);

  useUpdateEffect(() => {
    pageScrollTop = y;
  }, [y]);

  useMount(() => {
    document.documentElement.scrollTop = pageScrollTop;
  });

  return (
    <>
      <TitleBar
        title={platform.name}
        actions={
          <div className={classes.actionIcon}>
            <CourseSearch platform={platform} courseCollection={courseCollection} />
          </div>
        }
      />
      <Container maxWidth="md" className={classes.container}>
        <CourseList />
      </Container>
    </>
  );
}
