import { useMemo } from 'react';
import { createEntityCollection, encrypt } from '@src/lib';
import dedaoCourseList from '@src/data/dedao/index.json';
import igetcoolCourseList from '@src/data/igetcool/index.json';
import vistopiaCourseList from '@src/data/vistopia/index.json';

/**
 * @type {Record<PlatformEntity['code'], Array<Omit<CourseEntity, 'fuzzyId'>>>}
 */
const courseMap = {
  dedao: dedaoCourseList,
  igetcool: igetcoolCourseList,
  vistopia: vistopiaCourseList,
};

/**
 * 获取课程集合
 * @param {object} options
 * @param {PlatformEntity} options.platform
 */
export function useCourseCollection({ platform }) {
  const courseCollection = useMemo(() => {
    return createEntityCollection(
      courseMap[platform.code].map(x => ({ ...x, fuzzyId: encrypt(x.id) })),
      x => x.id,
    );
  }, [platform]);

  return { courseCollection };
}
